.level_principal{
    background-color: var(--darkreader-background);
    padding: 15px 0;
    text-align: center;
}
.level_principal > h2, .level_principal > h3{
    font-size: 3.5rem;
    color: rgb(10, 200, 238);
    text-align: center;
    width: -webkit-max-content;
    width: max-content;
    margin: 20px auto;
}
.level_principal > h3{
    color: var(--darkreader-text-gray);
    font-size: 2.5rem;
    -webkit-font-feature-settings: "smcp";
            font-feature-settings: "smcp";
    font-variant: small-caps;
}
.dumbbell{
    font-size: 2rem;
    rotate: -40deg;
    margin: 0 10px;
    color: var(--orange);
}
.dumbbell:nth-child(2){
    rotate: 40deg;
}
.level_container{
    background-color: #17191a;
    width: 97%;
    margin: 10px auto;
    border-radius: 20px;
    overflow: hidden;
    text-align: center;
    color: var(--darkreader-text-gray);
}
.level_container .datos{
    width: 95%;
    max-width: 1400px;
    margin: 10px auto;
    border-radius: 20px;
    display: flex;
}
.imagen{
    display: block;
    width: 30%;
    max-width: 400px;
    max-height: 300px;
    margin: auto;
}
.imagen > img{
    max-width: 100%;
    max-height: 300px;
    object-fit: cover;
    border: solid 1px black;
    border-radius: 20px;
}
.level_slider .container_slider_principal .slider_principal .slider_section img{
    height: auto;
    max-height: 300px;
    object-fit: contain;
    border: solid 1px black;
    border-radius: 20px;
    background-color: rgb(24, 24, 26);
    pointer-events: all;
}
.level_slider .container_slider_principal .slider_principal{
    background-color: transparent;
    height: 300px;
    max-height: none;
    display: flex;
    justify-content: center;
    align-items: center;
}
.level_slider .container_slider_principal{
    height: 300px;
}
.info{
    padding: 15px 0;
    margin: auto;
    margin-right: 0;
    width: 70%;
}
.info > p, .info > h4{
    width: 90%;
    max-width: 840px;
    margin: 0 auto;
}
.info > p{
    text-align: left;
    font-size: 1.5rem;
}
.info > h5{
    font-size: 2rem;
}
.info > a{
    text-decoration: none;
    font-size: 2rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    background: #ff7844;
    background: linear-gradient(45deg, #ff7844, #d93f87, #ff7844, #ffdd55, #ff7844, #d93f87, #ff7844, #ffdd55, #ff7844);
    background-size: 800%;
    color: transparent;
    -webkit-background-clip: text;
            background-clip: text;
    transition: all 0.3s ease;
    -webkit-animation: magicBtn 20s linear infinite;
            animation: magicBtn 20s linear infinite;
}
.info >a:hover{
    -webkit-animation-play-state: paused;
            animation-play-state: paused;
}
@-webkit-keyframes magicBtn{
    0%{
        background-position: 0 0;
    }
    100%{
        background-position: 800% 0;
    }
}
@keyframes magicBtn{
    0%{
        background-position: 0 0;
    }
    100%{
        background-position: 800% 0;
    }
}
.btn_next_level{
    padding: 10px 20px;
    -webkit-font-feature-settings: "smcp";
            font-feature-settings: "smcp";
    font-variant: small-caps;
    line-height: 100px;
}
@media screen and (max-width: 950px) {
    .level_container .datos{
        display: block;
    }
    .imagen{
        width: 100%;
    }
    .info{
        width: 100%;
    }
}
.container_slider_principal{
    -webkit-user-select: none;
            user-select: none;
    width: 100%;
    height: 50vw;
    max-height: 100vh;
    overflow: hidden;
    margin: auto;
    position: relative;
}
.img_slider_principal{
    pointer-events: none;
    width: 100%;
    height: 50vw;
    max-height: 100vh;
    object-fit: cover;
}
.slider_principal{
    background-color: rgb(24, 24, 26);
    height: 50vw;
    max-height: 100vh;
}
.slider_section{
    display: none;
}
.carrousel_active{
    display: block;
    visibility: visible;
    -webkit-animation: imgScale linear .2s forwards;
            animation: imgScale linear .2s forwards;
}
@-webkit-keyframes imgScale {
    0%{opacity: .5;}
    
    100%{opacity: 1;}
}
@keyframes imgScale {
    0%{opacity: .5;}
    
    100%{opacity: 1;}
}

.container_btn{
    background-color: transparent;
    border: none;
    position: absolute;
    top: 0;
    cursor: pointer;
    place-items: center;
    display: grid;
    height: 100%;
    width: 20%;
    z-index: 1;
}
.container_btn .arrow_icon{
    Color: rgba(255, 255, 255, 0.7);
    background-color: transparent;
    font-size: 4rem;
    font-family: monospace;
    border-radius: 50%;
}
.container_btn:hover .arrow_icon{
    background: rgba(0, 0, 0, .5);
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
    Color: rgba(255, 255, 255);
}
.slider_btn_left{
    left: 0;
}
.slider_btn_right{
    right: 0;
}
.slider_btn_left .arrow_icon{
    position: absolute;
    left: 10px;
}
.slider_btn_right .arrow_icon{
    position: absolute;
    right: 10px;
}
.container_info_trainer{
    position: absolute;
    top: 0;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: right;
}
.info_trainer{
    color: #fff;
    margin-right: 20%;
}
.info_trainer p{
    color: #fff;
    text-transform: uppercase;
    font-size: 1.3rem;
    margin-bottom: 20px;
}
.info_trainer h3{
    color: #fff;
    font-family: sans-serif;
    font-weight: bold;
    font-size: 5rem;
    margin-bottom: 20px;
}
.info_trainer h3 span{
    color: var(--orange);
    font-family: sans-serif;
}
.info_trainer button{
    z-index: 3;
    cursor: pointer;
    color: #fff;
    background-color: var(--orange);
    border: none;
    padding: 17px 30px 16px;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1rem;
}
.slider_text_animation{
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
    opacity: 0;
}
.slider_text_animation_animation{
    -webkit-animation: sliderAnimate .2s linear forwards;
            animation: sliderAnimate .2s linear forwards;
}
.slider_text_animation:nth-child(2){
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
}
.slider_text_animation:nth-child(3){
    -webkit-animation-delay: .4s;
            animation-delay: .4s;
}
@-webkit-keyframes sliderAnimate {
    0%{
        opacity: 0;
        -webkit-transform: translateY(100px);
                transform: translateY(100px);
    }
    100%{
        opacity: 1;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}
@keyframes sliderAnimate {
    0%{
        opacity: 0;
        -webkit-transform: translateY(100px);
                transform: translateY(100px);
    }
    100%{
        opacity: 1;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}
.slider_not_found > h5{
    font-size: 1.5rem;
    width: 90%;
    margin: auto;
}
@media screen and (max-width: 550px) {
    @-webkit-keyframes sliderAnimate {
        0%{
            opacity: 0;
            -webkit-transform: translateY(50px);
                    transform: translateY(50px);
        }
        100%{
            opacity: 1;
            -webkit-transform: translateY(0px);
                    transform: translateY(0px);
        }
    }
    @keyframes sliderAnimate {
        0%{
            opacity: 0;
            -webkit-transform: translateY(50px);
                    transform: translateY(50px);
        }
        100%{
            opacity: 1;
            -webkit-transform: translateY(0px);
                    transform: translateY(0px);
        }
    }
    .container_info_trainer{
        justify-content: center;
    }
    .info_trainer{
        margin-right: 0;
    }
    .info_trainer p{
        font-size: 1.3rem;
        margin-bottom: 10px;
    }
    .info_trainer h3{
        font-size: 3rem;
        margin-bottom: 10px;
    }
    .info_trainer button{
        font-size: 1rem;
    }
}
